export default {
	data() {
		return {
			imgUrl:getApp().globalData.imgUrl,
			loadEnd:false
		}
	},
	onLoad() {
		uni.showLoading({
			title: ''
		})
	},
	methods: {
		toMap(){
			uni.navigateTo({
				url:'storeList'
			})
		},
		imgLoad(e) {
			if (e.detail) {
				uni.hideLoading()
				this.loadEnd = true;
			}
		}
	}
}
